import React from "react";
import {
  withGoogleMap,
  GoogleMap,
  withScriptjs,
  InfoWindow,
  Marker,
} from "react-google-maps";
import Geocode from "react-geocode";
import Autocomplete from "react-google-autocomplete";
import TextField from "../compoonent/textField";

Geocode.setApiKey(process?.env?.REACT_APP_Map);
Geocode.enableDebug();
class Map extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      address: "",
      city: "",
      area: "",
      state: "",
      mapPosition: {
        lat: this?.props?.center?.lat,
        lng: this?.props?.center?.lng,
      },
      markerPosition: {
        lat: this?.props?.center?.lat,
        lng: this?.props?.center?.lng,
      },
    };
  }
  /**
   * Get the current address from the default map position and set those values in the state
   */
  componentDidUpdate() {
    return false;
  }
  componentDidMount() {
    Geocode.fromLatLng(
      // 29.953442569330118,
      this?.state?.mapPosition.lat,
      // 29.9532566531456
      this?.state?.mapPosition.lng
    ).then(
      (response) => {
        // console.log("omerrr");
        const address = response.results[0].formatted_address,
          addressArray = response.results[0].address_components,
          city = this.getCity(addressArray),
          area = this.getArea(addressArray),
          state = this.getState(addressArray);

        // console.log("city", city, area, state);

        this.setState({
          address: address ? address : "",
          area: area ? area : "",
          city: city ? city : "",
          state: state ? state : "",
        });
      },
      (error) => {
        console.error(error);
      }
    );
  }
  /**
   * Component should only update ( meaning re-render ), when the user selects the address, or drags the pin
   *
   * @param nextProps
   * @param nextState
   * @return {boolean}
   */
  shouldComponentUpdate(nextProps, nextState) {
    // return false;
    if (
      // this?.state?.markerPosition?.lat !== this?.props?.center?.lat
      this?.state?.address !== nextState?.address ||
      this?.state?.city !== nextState?.city ||
      this?.state?.area !== nextState?.area ||
      this?.state?.state !== nextState?.state
    ) {
      return true;
    } else if (this?.props?.center?.lat === nextProps?.center?.lat) {
      return false;
    }
  }
  /**
   * Get the city and set the city input value to the one selected
   *
   * @param addressArray
   * @return {string}
   */
  getCity = (addressArray) => {
    let city = "";
    for (let i = 0; i < addressArray?.length; i++) {
      if (
        addressArray?.[i]?.types?.[0] &&
        "administrative_area_level_2" === addressArray?.[i].types?.[0]
      ) {
        city = addressArray?.[i]?.long_name;
        return city;
      }
    }
  };
  /**
   * Get the area and set the area input value to the one selected
   *
   * @param addressArray
   * @return {string}
   */
  getArea = (addressArray) => {
    let area = "";
    for (let i = 0; i < addressArray?.length; i++) {
      if (addressArray?.[i]?.types?.[0]) {
        for (let j = 0; j < addressArray?.[i]?.types.length; j++) {
          if (
            "sublocality_level_1" === addressArray?.[i]?.types?.[j] ||
            "locality" === addressArray?.[i]?.types[j]
          ) {
            area = addressArray?.[i]?.long_name;
            return area;
          }
        }
      }
    }
  };
  /**
   * Get the address and set the address input value to the one selected
   *
   * @param addressArray
   * @return {string}
   */
  getState = (addressArray) => {
    let state = "";
    for (let i = 0; i < addressArray?.length; i++) {
      for (let i = 0; i < addressArray?.length; i++) {
        if (
          addressArray?.[i]?.types?.[0] &&
          "administrative_area_level_1" === addressArray?.[i]?.types?.[0]
        ) {
          state = addressArray?.[i]?.long_name;
          return state;
        }
      }
    }
  };
  /**
   * And function for city,state and address input
   * @param event
   */
  // onChange = (event) => {
  //   this.setState({ [event.target.name]: event.target.value });
  // };
  /**
   * This Event triggers when the marker window is closed
   *
   * @param event
   */
  onInfoWindowClose = (event) => {};
  /**
   * When the marker is dragged you get the lat and long using the functions available from event object.
   * Use geocode to get the address, city, area and state from the lat and lng positions.
   * And then set those values in the state.
   *
   * @param event
   */
  onMarkerDragEnd = (event) => {
    // this.setState({ address: "dmaodmpoa" });
    console.log("onMarkerDragEnd", event.latLng.lat());
    let newLat = event.latLng.lat(),
      newLng = event.latLng.lng(),
      addressArray = [];
    Geocode.fromLatLng(newLat, newLng).then(
      (response) => {
        console.log("resss", response.results[0].formatted_address);
        this.props.address(response.results[0].formatted_address);
        const address = response.results[0].formatted_address,
          addressArray = response.results[0].address_components,
          city = this.getCity(addressArray),
          area = this.getArea(addressArray),
          state = this.getState(addressArray);
        this.setState({
          address: address ? address : "",
          area: area ? area : "",
          city: city ? city : "",
          state: state ? state : "",
          markerPosition: {
            lat: event.latLng.lat(),
            lng: event.latLng.lng(),
          },
          mapPosition: {
            lat: event.latLng.lat(),
            lng: event.latLng.lng(),
          },
        });
      },
      (error) => {
        console.error(error);
      }
    );
  };

  onPlaceSelected = (place) => {
    this.props.address(place?.formatted_address);
    const address = place?.formatted_address,
      addressArray = place?.address_components,
      city = this.getCity(addressArray),
      area = this.getArea(addressArray),
      state = this.getState(addressArray),
      latValue = place?.geometry?.location.lat(),
      lngValue = place?.geometry?.location.lng();
    // Set these values in the state.
    this.setState({
      address: address ? address : "",
      area: area ? area : "",
      city: city ? city : "",
      state: state ? state : "",
      markerPosition: {
        lat: latValue,
        lng: lngValue,
      },
      mapPosition: {
        lat: latValue,
        lng: lngValue,
      },
    });
  };
  /**
   * When the marker is dragged you get the lat and long using the functions available from event object.
   * Use geocode to get the address, city, area and state from the lat and lng positions.
   * And then set those values in the state.
   *
   * @param event
   */
  //   onMarkerDragEnd = (event) => {
  //     console.log("event", event);
  //     let newLat = event.latLng.lat(),
  //       newLng = event.latLng.lng(),
  //       addressArray = [];
  //     Geocode.fromLatLng(newLat, newLng).then(
  //       (response) => {
  //         const address = response.results[0].formatted_address,
  //           addressArray = response.results[0].address_components,
  //           city = this.getCity(addressArray),
  //           area = this.getArea(addressArray),
  //           state = this.getState(addressArray);
  //         this.setState({
  //           address: address ? address : "",
  //           area: area ? area : "",
  //           city: city ? city : "",
  //           state: state ? state : "",
  //         });
  //       },
  //       (error) => {
  //         console.error(error);
  //       }
  //     );
  //   };
  render() {
    const AsyncMap = withScriptjs(
      withGoogleMap((props) => (
        <GoogleMap
          google={this?.props?.google}
          defaultZoom={this?.props?.zoom}
          center={{
            lat: this?.state?.mapPosition?.lat,
            lng: this?.state?.mapPosition?.lng,
          }}
        >
          {/* InfoWindow on top of marker */}
          <InfoWindow
            onClose={this.onInfoWindowClose}
            position={{
              lat: this.state.markerPosition.lat + 0.0018,
              lng: this.state.markerPosition.lng,
            }}
          >
            {/* <div> */}
            <span style={{ padding: 0, margin: 0 }}>
              {this.state.address}omer
            </span>
            {/* </div> */}
          </InfoWindow>
          <Marker
            google={this.props.google}
            name={"Dolores park"}
            draggable={true}
            onDragEnd={this.onMarkerDragEnd}
            position={{
              lat: this.state.markerPosition.lat,
              lng: this.state.markerPosition.lng,
            }}
          />
          <Marker />
          <Autocomplete
            // placeholder={this.state.address}
            style={{
              width: "98%",
              height: "40px",
              // paddingLeft: "16px",
              // marginTop: "2px",
              // marginBottom: "100px",
              backgroundColor: "#ececec",
              borderColor: "#000",
              borderWidth: 0,
              borderRadius: 2,
              fontSize: 12,
              paddingInline: "1%",
            }}
            onPlaceSelected={this.onPlaceSelected}
            types={["(regions)"]}
          />
        </GoogleMap>
      ))
    );
    let map;
    if (this?.props?.center?.lat !== undefined) {
      map = (
        <div>
          <div style={{ width: "100%" }}>
            <TextField
              placeHolder={this.state.address}
              label="Address"
              width={"100%"}
            />
            {/* <div className="form-group">
              <label htmlFor="">City</label>
              <input
                type="text"
                name="city"
                className="form-control"
                onChange={this.onChange}
                readOnly="readOnly"
                value={this.state.city}
              />
            </div>
            <div className="form-group">
              <label htmlFor="">Area</label>
              <input
                type="text"
                name="area"
                className="form-control"
                onChange={this.onChange}
                readOnly="readOnly"
                value={this.state.area}
              />
            </div>
            <div className="form-group">
              <label htmlFor="">State</label>
              <input
                type="text"
                name="state"
                className="form-control"
                onChange={this.onChange}
                readOnly="readOnly"
                value={this.state.state}
              />
            </div> */}
            {/* <div className="form-group">
              <label htmlFor="">Address</label>
              <input
                type="text"
                name="address"
                className="form-control"
                onChange={this.onChange}
                readOnly="readOnly"
                value={this.state.address}
              />
            </div> */}
          </div>
          <AsyncMap
            googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${process?.env?.REACT_APP_Map}&libraries=places`}
            loadingElement={<div style={{ height: `100%` }} />}
            containerElement={<div style={{ height: this.props.height }} />}
            mapElement={<div style={{ height: `100%` }} />}
          />
        </div>
      );
    } else {
      map = <div style={{ height: this.props.height }} />;
    }
    return map;
  }
}
export default Map;
